<template>
  <div class="basicInfo" style="top:3.9vh;">
    <div class="Btitle">
      <span class="basicData">实名认证</span>
      <span>
        <span
          :class="{saveBtn1:contactBlean,saveBtn2:!contactBlean,mainBtn:true}"
          @click="contactBlean=false"
        >个人认证</span>
      </span>
      <span>
        <span
          :class="{saveBtn2:contactBlean,saveBtn1:!contactBlean,mainBtn:true}"
          @click="contactBlean=true"
          style="margin-left:1vw;"
        >企业认证</span>
      </span>
    </div>
    <div class="contact" v-show="contactBlean" style="height:60.5vh;">
      <div class="subTitle">企业实名认证</div>
      <ul>
        <li>
          <span class="listTitle listTitle1">公司名称:</span>
          <input class="listInput margin-l" v-model="companyName" v-show="firmBl" />
          <span v-show="!firmBl">{{companyName}}</span>
        </li>
        <li class="listTitleLi">
          <span class="listTitle licenseNum listTitle1">营业执照号:</span>
          <input class="listInput margin-l" v-model="licenseName" v-show="firmBl" />
          <span v-show="!firmBl">{{licenseName}}</span>
        </li>
        <li class="permit" style="position:relative;">
          <span class="listTitle listTitle1">营业执照:</span>
          <div class="setPermitEl permitEl" :style="`background-image:url(${permitElImg});`">
            <div v-show="firmBl">
              <img src="@/assets/UserCenter/imgs/shangchuan.png" alt />
            </div>
            <p v-show="firmBl">点击上传文件</p>
            <p v-show="firmBl">仅支持jpg、png格式图片</p>
            <p v-show="firmBl">大小小于1M</p>
            <input class="fileInput" type="file" @change="upload" v-show="firmBl" />
          </div>
          <span class="lookImg" @click="maskBackgroundBl=true" v-show="firmBl">查看</span>
        </li>
        <li>
          <span class="listTitle listTitle1">企业行业:</span>
          <Selection
            class="selections margin-l"
            @selectValue="mainIndustryClick"
            :selections="mainIndustryObj"
            :sonValue="mainIndustryStr"
            v-show="firmBl"
          ></Selection>
          <span v-show="!firmBl">{{mainIndustryStr}}</span>
        </li>
        <li>
          <span class="listTitle listTitleMarginLeft listTitle1"></span>
          <Selection
            class="selections margin-l selectionsBt"
            @selectValue="industryClick"
            :selections="industryObj"
            :textBl="industryBl"
            :sonValue="industryStr"
            v-show="firmBl"
          ></Selection>
          <span v-show="!firmBl">{{industryStr}}</span>
        </li>
        <li>
          <span class="listTitle listTitle1">详细地址:</span>
          <input
            class="listInput margin-l selectionsBt"
            placeholder="请输入详细的通讯地址"
            v-model="addressName"
            v-show="firmBl"
          />
          <span v-show="!firmBl">{{addressName}}</span>
        </li>
      </ul>
      <span class="saveBtn3 mainBtn saveBtn6" @click="firmBl=true" v-show="!firmBl">
        <a>修改认证</a>
      </span>
      <div>
        <span class="saveBtn3 mainBtn fl saveBtn6" @click="firmApproveClick" v-show="firmBl">
          <a>提交认证</a>
        </span>
        <span class="saveBtn3 mainBtn fl saveBtn5" @click="firmBl=false" v-show="firmBl">
          <a>取消修改</a>
        </span>
      </div>
    </div>
    <div class="contact contact1" v-show="!contactBlean">
      <div class="subTitle">个人实名认证</div>
      <ul>
        <li v-for="(item,i) in personalObj" :key="i">
          <span class="listTitle">{{item.heading}}</span>
          <input class="listInput margin-l" v-show="certificationBl" v-model="item.content" />
          <span v-show="!certificationBl">{{item.content}}</span>
        </li>
      </ul>
      <span class="saveBtn3 mainBtn" @click="certificationBl=true" v-show="!certificationBl">
        <a>修改认证</a>
      </span>
      <span class="saveBtn3 mainBtn" @click="IApproveClick" v-show="certificationBl">
        <a>保存修改</a>
      </span>
      <span
        class="saveBtn3 mainBtn saveBtn5"
        @click="certificationBl=false"
        v-show="certificationBl"
      >
        <a>取消修改</a>
      </span>
      <div></div>
    </div>
    <div class="maskBackground" @click="maskBackgroundBl=false" v-show="maskBackgroundBl">
      <div class="allCenter2">
        <img :src="permitElImg" alt />
        <img src="@/assets/UserCenter/imgs/dele.png" alt class="deleImg" />
      </div>
    </div>
  </div>
</template>

<script>
// 引入行业分类
import NationalIndustryArr from "@/assets/UserCenter/js/NationalIndustryArr";
import Selection from "@/pages/UserCenter/UserItem/Selection.vue";
export default {
  name: "certification",
  components: { Selection },
  data() {
    return {
      mobileData: [],
      // 控制全屏查看营业执照的BL
      maskBackgroundBl: false,
      // 执照图片路径
      permitElImg: "",
      // 企业认证修改与默认BL
      firmBl: false,
      // 个人认证修改与默认BL
      certificationBl: false,
      // 国定行业分类
      NationalIndustryArr: NationalIndustryArr.NationalIndustryArr,
      // 选择的主行业
      mainIndustryStr: "",
      // 主行业Obj
      mainIndustryObj: {
        placeholder: "",
        w: "16vw",
        data: ""
      },
      // 选择的子行业
      industryStr: "无",
      // 控制子行业value是否显示---如果重新选中主行业，则子行业不显示
      industryBl: false,
      // 子行业obj
      industryObj: {
        placeholder: "",
        w: "16vw",
        data: ""
      },
      // 企业认证和个人认证切换按钮
      contactBlean: false,
      personalObj: [
        {
          heading: "真实姓名:",
          content: "无"
        },
        {
          heading: "身份证号:",
          content: "无"
        },
        {
          heading: "通讯地址:",
          content: "无"
        }
      ],
      // 企业名称
      companyName: "无",
      // 营业执照号
      licenseName: "无",
      // 选中的营业执照图片
      fileImg: "无",
      // 企业详细地址
      addressName: "无"
    };
  },
  methods: {
    // 个人认证保存修改
    IApproveClick() {
      // 保存前的判断，一项为false则return
      var regName = /^[\u4e00-\u9fa5]{2,13}$/;
      var regIdNo = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (
        !regName.test(this.personalObj[0].content) ||
        this.personalObj[0].content.length < 2 ||
        this.personalObj[0].content.length > 15
      ) {
        this.tips.show(["请输入正确的姓名"]);
        return;
      }
      if (!regIdNo.test(this.personalObj[1].content)) {
        this.tips.show(["请输入正确的身份证号码"]);
        return;
      }
      if (
        this.personalObj[2].content == "" ||
        this.personalObj[2].content.length > 50
      ) {
        this.tips.show(["地址信息不能为空并小于50个字符"]);
        return;
      }

      const pac = {
        realName: this.personalObj[0].content,
        idCards: this.personalObj[1].content,
        location: this.personalObj[2].content
      };
      this.$put("api/certification", {
        pac
      })
        .then(res => {
          if (res.code == "000000") {
            this.tips.show(["修改信息成功", true]);
            this.certificationBl = false;
            this.init();
          } else {
            this.tips.show(["修改信息失败", false]);
          }
        })
        .catch(err => {
          this.tips.show(["修改信息失败"]);
        });
    },
    // 企业认证点击事件
    firmApproveClick() {
      // 重重判断~
      if (this.companyName.length > 30 || this.companyName.length < 2) {
        this.tips.show(["请输入正确的公司名称"]);
        return;
      }
      if (!this.licenseName || this.licenseName.length > 18) {
        this.tips.show(["请输入正确的营业执照号"]);
        return;
      }
      if (this.addressName.length > 50 || this.addressName == "无") {
        this.tips.show(["详细地址不能为空并小于50字符"]);
        return;
      }
      if (this.fileImg == "无") {
        this.tips.show(["请上传营业执照"]);
        return;
      }

      const anbcc = {
        firmName: this.companyName,
        charterNum: this.licenseName,
        charterImg: this.permitElImg,
        industry: this.mainIndustryStr,
        sonIndustry: this.industryStr,
        location: this.addressName
      };

      this.$put("api/certification", {
        anbcc
      }).then(res => {
        if (res.code == "000000") {
          this.tips.show(["修改信息成功", true]);
          this.firmBl = false;
          this.init();
        } else {
          this.tips.show(["修改信息失败", false]);
        }
      });
    },
    // 行业点击事件，二级联动
    mainIndustryClick(valueObj) {
      if (
        this.mainIndustryStr != "" &&
        this.mainIndustryStr != valueObj.label
      ) {
        this.industryBl = true;
      }
      var mainIndustryArr = [];
      this.mainIndustryStr = valueObj.label;
      for (let i = 0; i < this.NationalIndustryArr.length; i++) {
        if (valueObj.label == this.NationalIndustryArr[i].name) {
          for (
            let j = 0;
            j < this.NationalIndustryArr[i].childrenArr.length;
            j++
          ) {
            mainIndustryArr.push({
              label: this.NationalIndustryArr[i].childrenArr[j],
              value: j
            });
          }
        }
      }
      this.industryObj.data = mainIndustryArr;
    },
    industryClick(valueObj) {
      this.industryBl = false;
      this.industryStr = valueObj.label;
    },
    // 初始化二级联动
    fn() {
      var mainIndustryArr = [];
      for (let j = 0; j < this.NationalIndustryArr.length; j++) {
        mainIndustryArr.push({
          label: this.NationalIndustryArr[j].name,
          value: j
        });
      }
      this.mainIndustryObj.data = mainIndustryArr;
    },
    // 图片选中事件，把图片切换并改变Boolean
    upload(event) {
      let files = event.target.files || event.dataTransfer.files;
      if (!files.length) {
        return;
      }
      this.fileImg = files[0];
      var url = window.url || window.webkitURL;
      //通过url对象将二进制文件创建成一个url的格式
      this.permitElImg = url.createObjectURL(this.fileImg);
    },
    // 初始化认证信息
    init() {
      this.$get("api/certification").then(res => {
        if (res.code === "000000") {
          this.mobileData = res.data;
          this.personalObj[0].content = this.judgmentValue(
            res.data.pac.realName
          );
          this.personalObj[1].content = this.judgmentValue(
            res.data.pac.idCards
          );
          this.personalObj[2].content = this.judgmentValue(
            res.data.pac.location
          );
          this.companyName = this.judgmentValue(res.data.anbcc.firmName);
          this.licenseName = this.judgmentValue(res.data.anbcc.charterNum);
          this.permitElImg = this.judgmentValue(res.data.anbcc.charterImg);
          this.mainIndustryStr = this.judgmentValue(res.data.anbcc.industry);
          this.addressName = this.judgmentValue(res.data.anbcc.location);
          this.industryStr = this.judgmentValue(res.data.anbcc.sonIndustry);
        }
      });
    },
    judgmentValue(val) {
      if (val) {
        return val;
      } else {
        return "无";
      }
    }
  },
  mounted() {
    this.fn();
    this.init();
  }
};
</script>

