<template>
  <div class="selection-component"
    :style="{width:selections.w}"
    v-click-outside="{fn:closIsDrop}">
    <div class="selection-show"
      @click="toggleDrop">
      <input class="unSelect"
        :placeholder="selections.placeholder"
        :value="textBl?'':inputVal"
        :style="{width:selections.w}">
      <span class="triangle"
        :class="{rotating:isDrop}">
        <img src="@/assets/UserCenter/imgs/xiala.png"
          alt=""
          v-if="!isDrop"
          class="triangleImg">
        <img src="@/assets/UserCenter/imgs/shangshou.png"
          alt=""
          v-if="isDrop"
          class="triangleImg">
      </span>
    </div>
    <div class="selection-list"
      v-if="isDrop"
      :style="{width:selections.w}">
      <EasyScroll>
        <ul>
          <li v-for="(item, index) in selections.data"
            :key="index"
            @click.stop="choose(index)">{{ item.label }}</li>
        </ul>
      </EasyScroll>
    </div>
  </div>
</template>

<script>
export default {
	// 接收的参数：textBl-控制文字是否显示、value-控制默认文字-selections-控制插件大小及下拉框内容
	props: ['selections', 'textBl', 'sonValue'],
	data() {
		return {
			isDrop: false,
			inputVal: ''
		};
	},
	methods: {
		closIsDrop() {
			this.isDrop = false;
		},
		toggleDrop() {
			this.isDrop = !this.isDrop;
		},
		choose(index) {
			this.isDrop = false;
			this.inputVal = this.selections.data[index].label;
			this.$emit('selectValue', this.selections.data[index]);
		}
	},
	mounted() {
		if (this.sonValue) {
			this.inputVal = this.sonValue;
		}
	},
	created() {
		if (this.selections.inputVal) {
			this.inputVal = this.selections.inputVal;
		}
	},
	watch: {
		textBl: {
			immediate: true,
			handler() {}
		},
		sonValue: {
			immediate: true,
			handler() {
				this.inputVal = this.sonValue;
			}
		}
	}
};
//使用规则
// 接收的参数：textBl-控制文字是否显示、value-控制默认文字-selections-控制插件大小及下拉框内容
//传参名称必须为selections,
//返回参数selectValue,
//    selections: {
//     placeholder: "",//是否有提示信息
//     w: "16vw",//整体宽度，用来保证input框和下拉页面宽度一致
//     inputVal:"",//是否需要显示值,若无则不需要传该属性,控制默认文字
//     data: [//下拉框数据
//       {
//         label: "test",//展示的文本
//         value: 1,//值
//       },
//       {
//         label: "test1",
//         value: 2
//       }
//     ]
//   }
</script>

<style>
.selection-component {
  position: relative;
}

.selection-component .selection-show input {
  background-color: #121721;
  border: solid 0.0625vw #475566;
  color: #fff;
  padding-left: 0.4vw;
  height: 2.8vh;
  font-size: 0.7vw;
  cursor: pointer;
}

.selection-component .selection-show input:focus {
  border: solid 0.0625vw #28b3fc;
}

.selection-component .selection-show .triangle {
}

.selection-component .selection-show .triangle.rotating {
  transform: rotate(180deg);
}

.selection-component .selection-list {
  position: absolute;
  border: solid 0.0625vw #475566;
  border-top: none;
  top: 3.7vh;
  z-index: 9;
}

.selection-component .selection-list ul {
  cursor: pointer;
  font-size: 0.7vw;
  background-color: #121721;
  border-top: none;
  text-align: left;
  max-height: 17vh;
}

.selection-component .selection-list ul li {
  height: 2.8vh;
  line-height: 2.8vh;
  padding-left: 0.6vw;
  position: relative;
  z-index: 9;
  background-color: #121721;
}

.selection-component .selection-list ul li:hover {
  background-color: #252e42;
}
.triangleImg {
  width: 0.5vw;
  height: 0.5vw;
  position: absolute;
  top: 0.9vw;
  right: 0.6vw;
  z-index: 0;
}
</style>
